/*================================================
Project Details CSS
=================================================*/
.project-details-image {
  position: relative;
  z-index: 1;
  margin-bottom: 30px;
  text-align: center;
}
.project-details-image img {
  width: 100%;
  height: 350px;
}

.project-details-image::before {
  content: '';
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background: #0e314c;
  opacity: 0;
  visibility: hidden;
  transition: .4s;
}

.project-details-image a {
  position: absolute;
  left: 0;
  right: 0;
  margin: 0 auto;
  top: 50%;
  color: #ffffff;
  margin-top: 0;
  opacity: 0;
  visibility: hidden;
  margin-top: 25px;
}

.project-details-image svg {
  position: absolute;
  left: 0;
  right: 0;
  margin: 0 auto;
  top: 50%;
  color: #ffffff;
  margin-top: 0;
  opacity: 0;
  visibility: hidden;
  margin-top: 25px;
}

.project-details-image a:hover {
  color: #44ce6f;
}

.project-details-image svg:hover {
  color: #44ce6f;
}

.project-details-image:hover::before {
  opacity: .90;
  visibility: visible;
}

.project-details-image:hover a {
  opacity: 1;
  visibility: visible;
  margin-top: -5px;
}

.project-details-image:hover svg {
  opacity: 1;
  visibility: visible;
  margin-top: -5px;
}

.project-details-desc h3 {
  font-size: 18px;
  margin-bottom: 15px;
  margin-top: 15px;
